import * as React from 'react';

const Button = ({ children, href, ...props }: React.HTMLProps<HTMLAnchorElement>) => {
	return (
		<a
			href={href}
			className="border-secondary text-secondary border-solid border-2 py-2 px-8 rounded-full"
			{...props}
		>
			{children}
		</a>
	);
};

export default Button;
